// app/(admin)/crear/page.tsx
"use client";

import { useState, useEffect, type FormEvent, type ChangeEvent } from "react";
import { useRouter } from "next/navigation";
import clsx from "clsx";

/* -------------------------------------------------------------------------- */
/*                                   Tipos                                    */
/* -------------------------------------------------------------------------- */

type PromoType = "descuento" | "2x1" | "nuevo" | "solo-hoy";

interface TextareaProps {
    label: string;
    value: string;
    onChange: (value: string) => void;
}

interface InputCommaProps {
    label: string;
    placeholder: string;
    value: string;
    onChange: (value: string) => void;
}

interface Promo {
    id: number;
    tipo: PromoType;
    valor?: number;
    nombre: string;
    descripcion: string;
    diasSemana: number[];
}

interface PromoFormProps {
    tipo: PromoType;
    valor: number;
    nombrePromo: string;
    descPromo: string;
    diasSemana: number[];
    msg: { ok?: string; err?: string };
    toggleDia: (d: number) => void;
    setTipo: (v: PromoType) => void;
    setValor: (v: number) => void;
    setNombrePromo: (v: string) => void;
    setDescPromo: (v: string) => void;
    handleSubmitPromo: (e: FormEvent) => void;
}

interface InputTextProps {
    label: string;
    value: string;
    onChange: (value: string) => void;
    placeholder?: string;
}

const TAG_OPTIONS = [
    "bebida",
    "frío",
    "dulce",
    "panadería",
    "refresco",
    "snack",
    "krusty",
    "salado",
    "chicle",
    "revista",
    "lectura",
    "caliente",
    "comida",
];

const DIAS_SEMANA = [
    "Domingo",
    "Lunes",
    "Martes",
    "Miércoles",
    "Jueves",
    "Viernes",
    "Sabado",
];

/* -------------------------------------------------------------------------- */
/*                          Helper localStorage Promo                         */
/* -------------------------------------------------------------------------- */

const getPromos = (): Promo[] =>
    JSON.parse(localStorage.getItem("kwikemart_promos") || "[]");

const savePromo = (promo: Promo) => {
    const promos = getPromos();
    localStorage.setItem(
        "kwikemart_promos",
        JSON.stringify([...promos, promo])
    );
};

/* -------------------------------------------------------------------------- */
/*                             Reloj digital LED                              */
/* -------------------------------------------------------------------------- */

function DigitalClock() {
    const [now, setNow] = useState(new Date());

    useEffect(() => {
        const id = setInterval(() => setNow(new Date()), 1_000);
        return () => clearInterval(id);
    }, []);

    const dia = DIAS_SEMANA[now.getDay()];
    const [h, m, s] = [now.getHours(), now.getMinutes(), now.getSeconds()].map(
        (n) => n.toString().padStart(2, "0")
    );

    return (
        <div
            className="select-none rounded-2xl border-4 border-amber-800 bg-black shadow-2xl"
            style={{
                width: 300,
                height: 170,
                boxShadow: "0 0 30px rgba(100,77,50,.33)",
            }}>
            <p
                className="mt-2 mb-3 text-center text-2xl font-extrabold tracking-wider text-blue-200 uppercase"
                style={{
                    textShadow: "0 1px 6px #60a5fa,0 2px 10px #0ea5e9",
                    letterSpacing: ".16em",
                }}>
                {dia}
            </p>
            <p
                className="text-center font-mono font-bold text-green-400 text-[2.5rem] md:text-[3rem]"
                style={{
                    textShadow:
                        "0 0 22px #22d3ee,0 0 4px #14b8a6,0 0 2px #0f766e",
                    letterSpacing: ".17em",
                }}>
                {h}:{m}:{s}
            </p>
        </div>
    );
}

/* -------------------------------------------------------------------------- */
/*                 Burbujas de mensaje “Apu” (estáticas)                      */
/* -------------------------------------------------------------------------- */

const ApuMsgProducto = (
    <SpeechBubble>
        ¡Excelente elección, joven comerciante! Con este producto tu estantería
        brillará más que mis Squishees de uva… <b>¡Oh yeah!</b>
    </SpeechBubble>
);
const ApuMsgPromo = (
    <SpeechBubble>
        ¡Una promoción digna de Springfield! Los clientes harán fila; y si no,
        siempre quedan donas y Squishees para tentar sus bolsillos.{" "}
        <b>¡Viva la economía Kwik-E!</b>
    </SpeechBubble>
);

function SpeechBubble({ children }: { children: React.ReactNode }) {
    return (
        <div className="relative mb-6 mt-10">
            <div className="relative left-4 w-[97%] rounded-lg border-2 border-blue-400 bg-white px-8 py-4 shadow-md">
                <p className="text-lg font-semibold text-gray-800">
                    {children}
                </p>
                {/* púa */}
                <div className="absolute -bottom-[10px] left-6 h-0 w-0 border-l-[5px] border-l-transparent border-r-[20px] border-r-transparent border-t-[10px] border-t-blue-400" />
                <div className="absolute -bottom-[8px] left-[calc(1rem+1px)] h-0 w-0 border-l-[5px] border-l-transparent border-r-[20px] border-r-transparent border-t-[8px] border-t-white" />
            </div>
        </div>
    );
}

/* -------------------------------------------------------------------------- */
/*                               Componente UI                                */
/* -------------------------------------------------------------------------- */

export default function AdminCrearPage() {
    /* ------------------------- Selector de formulario ------------------------ */
    const [form, setForm] = useState<"producto" | "promo">("producto");

    /* ------------------------- Estado — Producto ----------------------------- */
    const router = useRouter();
    const [previewSrc, setPreviewSrc] = useState<string>();
    const [file, setFile] = useState<File>();
    const [nombre, setNombre] = useState("");
    const [precio, setPrecio] = useState("");
    const [etiquetas, setEtiquetas] = useState<string[]>([]);
    const [descripcion, setDescripcion] = useState("");
    const [sabores, setSabores] = useState("");
    const [tamanos, setTamanos] = useState("");

    const handleFile = (e: ChangeEvent<HTMLInputElement>) => {
        const f = e.target.files?.[0];
        if (!f) return;
        setFile(f);
        setPreviewSrc(URL.createObjectURL(f));
    };
    const toggleTag = (tag: string) =>
        setEtiquetas((prev) =>
            prev.includes(tag) ? prev.filter((t) => t !== tag) : [...prev, tag]
        );

    const handleSubmitProducto = (e: FormEvent) => {
        e.preventDefault();
        if (!file) return alert("Debes subir una imagen");

        const producto = {
            id: Date.now(),
            nombre,
            precio: parseFloat(precio),
            imagen: `/images/${file.name}`, // asume subida manual al static dir
            etiquetas,
            descripcion,
            opciones: {
                sabor: sabores
                    .split(",")
                    .map((s) => s.trim())
                    .filter(Boolean),
                tamaño: tamanos
                    .split(",")
                    .map((t) => t.trim())
                    .filter(Boolean),
            },
        };

        const productos = JSON.parse(
            localStorage.getItem("kwikemart_productos") || "[]"
        );
        localStorage.setItem(
            "kwikemart_productos",
            JSON.stringify([...productos, producto])
        );
        router.push("/admin/visualizar");
    };

    /* ------------------------- Estado — Promo ------------------------------- */
    const [tipo, setTipo] = useState<PromoType>("descuento");
    const [valor, setValor] = useState(10);
    const [nombrePromo, setNombrePromo] = useState("");
    const [descPromo, setDescPromo] = useState("");
    const [diasSemana, setDiasSemana] = useState<number[]>([]);
    const [msg, setMsg] = useState<{ ok?: string; err?: string }>({});

    const toggleDia = (d: number) =>
        setDiasSemana((prev) =>
            prev.includes(d) ? prev.filter((x) => x !== d) : [...prev, d]
        );

    const handleSubmitPromo = (e: FormEvent) => {
        e.preventDefault();
        setMsg({});
        if (!nombrePromo.trim() || !descPromo.trim() || diasSemana.length === 0)
            return setMsg({
                err: "Completa todos los campos y elige al menos un día.",
            });

        if (tipo === "descuento" && (valor < 1 || valor > 99))
            return setMsg({ err: "El descuento debe estar entre 1 % y 99 %" });

        savePromo({
            id: Date.now(),
            tipo,
            valor: tipo === "descuento" ? valor : undefined,
            nombre: nombrePromo,
            descripcion: descPromo,
            diasSemana: [...diasSemana].sort(),
        });

        setMsg({ ok: "¡Promoción creada!" });
        setNombrePromo("");
        setDescPromo("");
        setDiasSemana([]);
        setValor(10);
        setTipo("descuento");
        setTimeout(() => router.push("/admin/visualizar"), 1_200);
    };

    /* ---------------------------- Render ------------------------------------ */
    return (
        <section className="relative min-h-[700px] p-6 pb-22">
            <div className="mx-auto flex max-w-7xl flex-col gap-8 md:flex-row md:items-stretch">
                {/* Centro (selector + form) */}
                <div className="flex flex-1 flex-col items-center">
                    {form === "producto" ? ApuMsgProducto : ApuMsgPromo}

                    {/* Tabs selector */}
                    <div className="mb-8 flex w-full max-w-lg gap-4">
                        {["producto", "promo"].map((t) => (
                            <button
                                key={t}
                                onClick={() =>
                                    setForm(t as "producto" | "promo")
                                }
                                className={clsx(
                                    "flex-1 rounded-t-lg border-b-4 px-6 py-2 font-bold transition",
                                    form === t
                                        ? t === "producto"
                                            ? "bg-yellow-400 border-yellow-700 text-yellow-900"
                                            : "bg-blue-400 border-blue-700 text-blue-900"
                                        : "bg-white border-gray-200 text-gray-600 hover:bg-gray-100"
                                )}>
                                {t === "producto"
                                    ? "Crear Producto"
                                    : "Crear Promoción"}
                            </button>
                        ))}
                    </div>

                    {/* Form producto / promo */}
                    <div className="flex w-full justify-center">
                        {form === "producto" ? (
                            <ProductoForm
                                {...{
                                    previewSrc,
                                    nombre,
                                    precio,
                                    etiquetas,
                                    descripcion,
                                    sabores,
                                    tamanos,
                                    handleFile,
                                    toggleTag,
                                    setNombre,
                                    setPrecio,
                                    setDescripcion,
                                    setSabores,
                                    setTamanos,
                                    handleSubmitProducto,
                                }}
                            />
                        ) : (
                            <PromoForm
                                {...{
                                    tipo,
                                    valor,
                                    nombrePromo,
                                    descPromo,
                                    diasSemana,
                                    msg,
                                    toggleDia,
                                    setTipo,
                                    setValor,
                                    setNombrePromo,
                                    setDescPromo,
                                    handleSubmitPromo,
                                }}
                            />
                        )}
                    </div>
                </div>

                {/* Columna reloj (desktop) */}
                <div className="hidden min-w-[190px] md:flex md:w-[25%] flex-col items-center justify-center">
                    <DigitalClock />
                </div>
            </div>

            {/* Reloj mobile */}
            <div className="mt-12 flex justify-center md:hidden">
                <DigitalClock />
            </div>
        </section>
    );
}

/* -------------------------------------------------------------------------- */
/*                     Sub-componentes formularios UI                         */
/* -------------------------------------------------------------------------- */

interface ProductoFormProps {
    previewSrc?: string;
    nombre: string;
    precio: string;
    etiquetas: string[];
    descripcion: string;
    sabores: string;
    tamanos: string;
    handleFile: (e: ChangeEvent<HTMLInputElement>) => void;
    toggleTag: (tag: string) => void;
    setNombre: (v: string) => void;
    setPrecio: (v: string) => void;
    setDescripcion: (v: string) => void;
    setSabores: (v: string) => void;
    setTamanos: (v: string) => void;
    handleSubmitProducto: (e: FormEvent) => void;
}

function ProductoForm(props: ProductoFormProps) {
    const {
        previewSrc,
        nombre,
        precio,
        etiquetas,
        descripcion,
        sabores,
        tamanos,
        handleFile,
        toggleTag,
        setNombre,
        setPrecio,
        setDescripcion,
        setSabores,
        setTamanos,
        handleSubmitProducto,
    } = props;
    return (
        <div className="mb-12 w-full max-w-2xl rounded-lg bg-white p-8 shadow-lg">
            <h1 className="mb-6 text-2xl font-bold">Agregar Nuevo Producto</h1>
            <form onSubmit={handleSubmitProducto} className="space-y-6">
                {/* Nombre + precio */}
                <div className="grid grid-cols-1 gap-4 md:grid-cols-4">
                    <div className="md:col-span-3">
                        <label className="mb-1 block font-semibold">
                            Nombre
                        </label>
                        <input
                            value={nombre}
                            onChange={(e) => setNombre(e.target.value)}
                            required
                            className="w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
                        />
                    </div>
                    <div>
                        <label className="mb-1 block font-semibold">
                            Precio
                        </label>
                        <input
                            type="number"
                            step="0.01"
                            value={precio}
                            onChange={(e) => setPrecio(e.target.value)}
                            required
                            className="w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
                        />
                    </div>
                </div>

                {/* Tags */}
                <TagSelector {...{ etiquetas, toggleTag }} />

                {/* Descripción */}
                <Textarea
                    label="Descripción"
                    value={descripcion}
                    onChange={setDescripcion}
                />

                {/* Opciones */}
                <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
                    <InputComma
                        label="Sabores"
                        placeholder="p.ej. Cereza, Uva, Cola"
                        value={sabores}
                        onChange={setSabores}
                    />
                    <InputComma
                        label="Tamaños"
                        placeholder="p.ej. Pequeño, Mediano, Grande"
                        value={tamanos}
                        onChange={setTamanos}
                    />
                </div>

                {/* Imagen */}
                <div>
                    <label className="mb-1 block font-semibold">Imagen</label>
                    <input
                        type="file"
                        accept="image/*"
                        onChange={handleFile}
                        required
                        className="mb-2 block"
                    />
                    {previewSrc && (
                        <img
                            src={previewSrc}
                            alt="preview"
                            className="h-40 w-40 rounded border object-cover"
                        />
                    )}
                </div>

                <button
                    type="submit"
                    className="w-full rounded-lg bg-green-500 py-2 font-semibold text-white transition hover:bg-green-600">
                    Guardar Producto
                </button>
            </form>
        </div>
    );
}

/* ------------------------- Componentes helpers UI ------------------------- */

const TagSelector = ({
    etiquetas,
    toggleTag,
}: {
    etiquetas: string[];
    toggleTag: (t: string) => void;
}) => (
    <div>
        <label className="mb-2 block font-semibold">Categorías</label>
        <div className="flex flex-wrap gap-2">
            {TAG_OPTIONS.map((tag) => (
                <button
                    key={tag}
                    type="button"
                    onClick={() => toggleTag(tag)}
                    className={clsx(
                        "rounded-full border px-4 py-1 transition",
                        etiquetas.includes(tag)
                            ? "bg-green-400 border-green-600 text-white"
                            : "bg-white border-gray-300 text-gray-800 hover:bg-gray-100"
                    )}>
                    {tag.charAt(0).toUpperCase() + tag.slice(1)}
                </button>
            ))}
        </div>
    </div>
);

const Textarea = ({ label, value, onChange }: TextareaProps) => (
    <div>
        <label className="mb-1 block font-semibold">{label}</label>
        <textarea
            value={value}
            onChange={(e) => onChange(e.target.value)}
            className="h-24 w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
        />
    </div>
);

const InputComma = ({
    label,
    placeholder,
    value,
    onChange,
}: InputCommaProps) => (
    <div>
        <label className="mb-1 block font-semibold">
            {label}{" "}
            <span className="text-sm text-gray-500">(coma-separados)</span>
        </label>
        <input
            value={value}
            onChange={(e) => onChange(e.target.value)}
            placeholder={placeholder}
            className="w-full rounded border-2 border-green-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-green-400"
        />
    </div>
);

/* ------------------------- Formulario PROMO ------------------------------ */

function PromoForm(props: PromoFormProps) {
    const {
        tipo,
        valor,
        nombrePromo,
        descPromo,
        diasSemana,
        msg,
        toggleDia,
        setTipo,
        setValor,
        setNombrePromo,
        setDescPromo,
        handleSubmitPromo,
    } = props;

    return (
        <div className="w-full max-w-xl rounded-lg bg-white p-8 shadow-lg">
            <h1 className="mb-6 text-2xl font-bold">Crear Nueva Promoción</h1>
            <form onSubmit={handleSubmitPromo} className="space-y-6">
                <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
                    <div>
                        <label className="mb-1 block font-semibold">
                            Tipo de Promoción
                        </label>
                        <select
                            value={tipo}
                            onChange={(e) => {
                                const v = e.target.value as PromoType;
                                setTipo(v);
                                if (v !== "descuento") setValor(0);
                            }}
                            className="w-full rounded border-2 border-blue-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400">
                            <option value="descuento">Descuento %</option>
                            <option value="2x1">2×1</option>
                            <option value="nuevo">Nuevo</option>
                            <option value="solo-hoy">Sólo hoy</option>
                        </select>
                    </div>
                    {tipo === "descuento" && (
                        <div>
                            <label className="mb-1 block font-semibold">
                                Porcentaje (%)
                            </label>
                            <input
                                type="number"
                                min={1}
                                max={99}
                                value={valor}
                                onChange={(e) =>
                                    setValor(Number(e.target.value))
                                }
                                className="w-full rounded border-2 border-blue-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
                                required
                            />
                        </div>
                    )}
                </div>

                <InputText
                    label="Nombre"
                    value={nombrePromo}
                    onChange={setNombrePromo}
                    placeholder="Ej. Promo Squishee"
                />
                <Textarea
                    label="Descripción"
                    value={descPromo}
                    onChange={setDescPromo}
                />

                <div>
                    <label className="mb-1 block font-semibold">
                        Días de la semana
                    </label>
                    <div className="flex flex-wrap gap-2">
                        {DIAS_SEMANA.map((d, i) => (
                            <button
                                key={d}
                                type="button"
                                onClick={() => toggleDia(i)}
                                className={clsx(
                                    "rounded-full border px-4 py-1 font-semibold transition",
                                    diasSemana.includes(i)
                                        ? "bg-blue-400 border-blue-600 text-white"
                                        : "bg-white border-gray-300 text-gray-800 hover:bg-gray-100"
                                )}>
                                {d}
                            </button>
                        ))}
                    </div>
                </div>

                {msg.err && (
                    <p className="font-semibold text-red-500">{msg.err}</p>
                )}
                {msg.ok && (
                    <p className="font-semibold text-green-600">{msg.ok}</p>
                )}

                <button
                    type="submit"
                    className="w-full rounded-lg bg-blue-500 py-2 font-semibold text-white transition hover:bg-blue-600">
                    Guardar Promoción
                </button>
            </form>
        </div>
    );
}

const InputText = ({ label, value, onChange, placeholder }: InputTextProps) => (
    <div>
        <label className="mb-1 block font-semibold">{label}</label>
        <input
            value={value}
            onChange={(e) => onChange(e.target.value)}
            placeholder={placeholder}
            className="w-full rounded border-2 border-blue-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
            required
        />
    </div>
);
